/* App.css*/

.App {
	text-align: center;
}

/* Styling universal selector */
* {
	/* margin-top: 50px; */
	margin: 0;
	/* margin-top: 10px; */

	padding: 0;
	box-sizing: border-box;
}

/* Style body element */
body {
	/* min-height: 50vh;
	display: flex;
	align-items: center;
	text-align: center;
	justify-content: center;
	background: hsl(0, 0%, 100%); */
	font-family: "Poppins", sans-serif;
}

.notes {button {
	border-radius: 0 5px 5px 0;
	padding: 5px;

	font-size: large;
  border-style: none;
	color: #000000;
	background-color: rgba(255, 255, 255, 0.465);
}}

/* Styling card container */
.card {
	/* min-height: 50vh; */
  display: flex;
  flex-direction: row;
	width: 100%;
}

.head {
	/* position: relative; */
	/* max-height: fit-content; */
	/* min-width: 33rem; */
	width: 100%;
	background: rgb(242, 255, 102);

	padding: 1rem;
}


/* Input box and button */
.add {
	padding: 5%;
	text-align: center;
	padding-left: 6%;
	/* margin: 10%; */
	h3{
		text-align: center;
		margin: 10px;
	}
	input{
		margin: 10px;
		padding: 5px;
		border-radius: 20px;
	}
	button
	{
		color: #000000;
		font-weight: 700;
		background-color: aquamarine;
		border-radius: 5px;
		cursor: pointer;
	}
}

input {
	margin-right: 1%;
}

#title {
	width: 100%;
	font-size: larger;
}

#description {
	display: inline-block;
	font-size: large;
	width: 100%;
	min-height: 100px;
	overflow-y: hidden;
	text-align: start;
}



/* style accordion */
.notes {
	margin: 5%;
	text-align: left;
}

/* style for accordion items */
.notes-item {
	display: flex;
  border-radius: 5px;
	z-index: 100;
  margin: 5px;
	/* padding: 2%; */
	/* border-bottom: 1px solid #4e7b48; */
	font-size: small;
	/* margin: 2%; */
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

/* style for accordion items */
.accordion-item {
	border-bottom: 1px solid #ddd;
	font-size: larger;
}

.h123
{
  width: 80%;
}
.h1234
{
  width: 20%;
}

.notestoself, .notesofficial, .noteshome
{
	border-style:dashed;
	border-color:#54545487 ;
	border-width: 0 1px 0 1px;

}